@import '../../styles/vars';

.home {
  padding-top: 3rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  &-container-movie {
    margin: 1rem auto;
  }

  &-poster {
    width: 150px;
    display: block;
    margin: 0 auto;
    border-radius: 15px;
  }

  &-titleMovie {
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .home {
    padding-top: 0;
    margin-left: 265px;
  }
}